.hero{
    width: 100vw;
    height: 100vh;
    overflow: visible;
    background-image: url("./images/bg/bg.png");
    background-size: cover;
    position: relative;
    &__nav{
        position: absolute;
        display: flex; 
        align-items: center; 
        justify-content: space-between;
        width: calc(100% - 80px);
        padding: 32px 40px 0px 40px;
        &__logo{
            width: 177px;
            height: 17.78px;
        }
        &__menu{
            display: flex;
            align-items: center;
            &__item{
                padding-right: 48px;
                @include poppins16_500();
                color: var(--white-color);
            }
            &__button{
                background-color: var(--iris-color);
                border-radius: 16px;
                p{
                    padding: 14px 32px 14px 32px;
                    @include poppins16_500();
                    color: var(--white-color);
                }
            }
        }
    }
    &__content{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        &__circle{
            width: 960px;
            height: 960px;
            border-radius: 100%;
            background: #05051bf1;
            display: flex;
            justify-content: center;
            align-items: center;
            &__ring{
                width: 778px;
                height: 778px;
                border-radius: 100%;
                border: 2px solid var(--iris-color);
                display: flex;
                justify-content: center;
                align-items: center;
                &__text{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                    h1{
                        @include poppins56();
                        max-width: 634px;
                        text-align: center;
                        color: var(--white-color);
                        padding-bottom: 40px;
                    }
                    p{
                        @include poppins14_500();
                        color: var(--white-color);
                        border-bottom: 1px solid var(--white-color);
                    }
                    p::before{
                        content: "";
                        height: 1px;
                        width: 100%;
                        background: var(--white-color);
                    }
                }
            }
        }
    }
    &__shape{
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        vertical-align: center;
        left: 504px;
        top: 0px;
    }
    p{
        text-transform: uppercase;
    }
}